import request from './api'
import Cookie from 'js-cookie'


export let addFn = (item) => {
  console.log(item,12323123);
  let slug = item.slug;
  request.interceptors.request.use(function (config) {
    config.headers.Authorization = `Token ${Cookie.get("auth")}`
    return config;
  }, function (error) {
    return Promise.reject(error);
  });
  return request({
    method: "POST",
    url: `/api/articles/${slug}/favorite`
    
  })
}

export let deleteFn = (item) => {
  let slug = item.slug;
  request.interceptors.request.use(function (config) {
    config.headers.Authorization = `Token ${Cookie.get("auth")}`
    return config;
  }, function (error) {
    return Promise.reject(error);
  });
  return request({
    method: "DELETE",
    url:`/api/articles/${slug}/favorite`
  })
}