import React, {Component} from 'react';
import Header from './views/Header'
import { BrowserRouter as Router, Route, Switch } from "react-router-dom"
import "./main.css";
import Footer from "./views/Footer";
import Home from './views/Home'
import Login from './views/Login'
import Set from './views/Set'
import Profile from './views/Profile'
import Arcitle from './views/Arcitle'
import Editor from './views/Editor'
import Cookie from 'js-cookie'


class App extends Component {
  render() {
    if (Cookie.get("auth")) {
        return (
        <Router>
          <Header />
          <Switch>
            <Route exact path="/">
              <Home/>
            </Route>
            <Route  path="/editor">
              <Editor/>
            </Route>
            <Route  path="/setting" component={ Set }/>
            <Route  path="/login" component={ Login }/>
            <Route  path="/resign" component={ Login }/>
            <Route  path="/profile" component={ Profile }/>
            <Route  path="/arcitle" component={ Arcitle }/>
          </Switch>
          <Footer/>
        </Router>
      );
    } else {
      return (
        <Router>
          <Header />
          <Switch>
            <Route exact path="/">
              <Home/>
            </Route>
            <Route  path="/editor">
              <Editor/>
            </Route>
            <Route  path="/setting" component={ Login }/>
            <Route  path="/login" component={ Login }/>
            <Route  path="/resign" component={ Login }/>
            <Route  path="/profile" component={ Login }/>
            <Route  path="/arcitle" component={ Login }/>
          </Switch>
          <Footer/>
        </Router>
      );
    }
    
  }
}

export default App;