import React, { Component } from 'react';
import { NavLink } from 'react-router-dom'
import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'

class Header extends Component {
  constructor() {
    super();
    this.state = {
      auth:""
    }
  }

  componentDidMount() {
    let that = this;
    Pubsub.subscribe("auth", (msg, data) => {
      console.log(1111);
      that.setState({
        auth:data
      })
    })
  }
  render() {
    if (Cookie.get("auth")) {
      return (
      <div>
        <nav className="navbar navbar-light">
          <div className="container">
            <NavLink className="navbar-brand" to="/" activeClassName="active">conduit</NavLink>
            <ul className="nav navbar-nav pull-xs-right">
              <li className="nav-item">
                <NavLink className="nav-link active" to="/" activeClassName="active">Home</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/editor" activeClassName="active">
                  <i className="ion-compose"></i>&nbsp;New Post
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/setting" activeClassName="active">
                  <i className="ion-gear-a"></i>&nbsp;Settings
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/login" activeClassName="active">Sign In</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/resign" activeClassName="active">Sign up</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link ng-binding" ui-sref-active="active" ui-sref="app.profile.main({username:$ctrl.currentUser.username})" to="/profile" activeClassName="active">
                  <img ng-src="https://ftp.bmp.ovh/imgs/2021/04/5e93944c4f55c78c.jpg" src="https://ftp.bmp.ovh/imgs/2021/04/5e93944c4f55c78c.jpg"
                  className="user-pic"  
                  alt="图片" />
                  Wzm</NavLink>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  
    } else {
      return (
      <div>
        <nav className="navbar navbar-light">
          <div className="container">
            <NavLink className="navbar-brand" to="/" activeClassName="active">conduit</NavLink>
            <ul className="nav navbar-nav pull-xs-right">
              <li className="nav-item">
                <NavLink className="nav-link active" to="/" activeClassName="active">Home</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/login" activeClassName="active">Sign In</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/resign" activeClassName="active">Sign up</NavLink>
              </li>
              
            </ul>
          </div>
        </nav>
      </div>
    );
  
    }
  } 
}

export default Header;