import React, { Component } from 'react';
import request from '../../request/api';
import { addFn, deleteFn } from '../../request/favorite'
import Cookie from 'js-cookie'

class Home extends Component {
  constructor() {
    super();
    this.state = {
      data: [],
      pages: [],
      current: 1,
      count:null
    }
    this.favorite = this.favorite.bind(this)
  }

  componentDidMount() {
    if (Cookie.get("auth")) {
        request.interceptors.request.use(function (config) {
        config.headers.Authorization = `Token ${Cookie.get("auth")}`
        return config;
      }, function (error) {
        return Promise.reject(error);
        });
      request({
      method: "GET",
      url: "/api/articles",
      params: {
        
      }
    }).then(res => {
      console.log(res);
      let page = [];
      for (let i = 0; i < res.data.articlesCount/20; i++){
        page.push(i);
      }
      this.setState({
        data: res.data.articles,
        pages:page
      });
    })
    }
    
  }
  async favorite(data) {
    if (data.favorited) {
      await deleteFn(data).then(res => { this.setState({count:res.data.article.favoritesCount}) })
      data.favorited = false;
    } else {
      await addFn(data).then(res => { this.setState({count:res.data.article.favoritesCount}) });
      data.favorited = true;
    }
  }
  render() {
    return (
      <div>
        <div className="home-page">
            <div className="banner">
              <div className="container">
                <h1 className="logo-font">conduit</h1>
                <p>A place to share your knowledge.</p>
              </div>
            </div>
            <div className="container page">
              <div className="row">
                <div className="col-md-9">
                  <div className="feed-toggle">
                    <ul className="nav nav-pills outline-active">
                      <li className="nav-item">
                        <a className="nav-link disabled" href="">Your Feed</a>
                      </li>
                      <li className="nav-item">
                        <a className="nav-link active" href="">Global Feed</a>
                      </li>
                    </ul>
                </div>
                

                {
                  this.state.data.map((item, index) => {
                    return <div className="article-preview" key={index}>
                              <div className="article-meta">
                        <a href="profile.html"><img src={ item.author.image}/></a>
                                <div className="info">
                          <a href="" className="author">{ item.author.username}</a>
                          <span className="date">{ item.createdAt}</span>
                                </div>
                                <button className="btn btn-outline-primary btn-sm pull-xs-right" onClick={()=>{
                                  this.favorite(item);  
                                }}>
                                  <i className="ion-heart"></i> {this.state.count}
                                </button>
                              </div>
                              <a href="" className="preview-link">
                              <h1>{ item.title}</h1>
                              <p>{ item.description}</p>
                                <span>Read more...</span>
                              </a>
                            </div>
                  })
                }

                <ul className="pagination">
                  {
                    this.state.pages.map((item, index) => {
                      return <li className={ this.state.current==index+1?"page-item active":"page-item"} key={index}>
                        <a className="page-link" href="">{ item+1 }</a>
                      </li>
                    })
                  }
                </ul>
                

                 
              </div>
                <div className="col-md-3">
                  <div className="sidebar">
                    <p>Popular Tags</p>
                    <div className="tag-list">
                      <a href="" className="tag-pill tag-default">programming</a>
                      <a href="" className="tag-pill tag-default">javascript</a>
                      <a href="" className="tag-pill tag-default">emberjs</a>
                      <a href="" className="tag-pill tag-default">angularjs</a>
                      <a href="" className="tag-pill tag-default">react</a>
                      <a href="" className="tag-pill tag-default">mean</a>
                      <a href="" className="tag-pill tag-default">node</a>
                      <a href="" className="tag-pill tag-default">rails</a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
      </div>
    );
  }
}

export default Home;